
















import Vue from "vue";
import { Component } from "vue-property-decorator";
import { ChatService } from "@/x-vue/services/chat/chat.service";
import { ApiService } from "@/x-vue/services/api.service";
import { AppService } from "@/service/app.service";

import ChatRoomList from "@/x-vue/components/chat/ChatRoomList.vue";
import { Err } from "@/x-vue/services/defines";

@Component({
  components: {
    ChatRoomList,
  },
})
export default class extends Vue {
  app = AppService.instance;
  chat = ChatService.instance;
  idxOrEmail = "";

  mounted(): void {
    console.log("chat; ", this.chat);
  }

  async onBeginChat(): Promise<void> {
    console.log("idxOrEmail; ", this.idxOrEmail);
    try {
      const user = await ApiService.instance.otherUserProfile(this.idxOrEmail);
      console.log("user; ", user);
      if (!user.firebaseUid) {
        return this.app.error(Err.user_has_no_firebase_uid);
      }
      this.$router.push({ path: "chat-message", query: { uid: user.firebaseUid, hatch: "true" } });
    } catch (e) {
      this.app.error(e);
    }
  }

  async onSameRoomChat(): Promise<void> {
    console.log("idxOrEmail; ", this.idxOrEmail);
    try {
      const user = await ApiService.instance.otherUserProfile(this.idxOrEmail);
      console.log("user; ", user);
      if (!user.firebaseUid) {
        return this.app.error(Err.user_has_no_firebase_uid);
      }
      this.$router.push({ path: "chat-message", query: { uid: user.firebaseUid } });
    } catch (e) {
      this.app.error(e);
    }
  }
}
